<template>
  <div class="my">
    <van-popup
        class="music-page"
        v-model="$store.state.isShow"
        duration="0.2"
        position="bottom"
        :style="{ height: '100%' }"
    >

      <div>
        <top-bar class="top-bar" :title="topStyle?userDetail.name:''" :style="topStyle"/>
      </div>

      <div class="user-page">
        <van-image class="img-back" width="100%" height="" :src="userDetail.backUrl"/>
        <div class="user-detail">
          <div class="user-img">
            <van-image width="90px" :src="userDetail.avatarUrl"/>
          </div>
          <div class="user-name">
            <p>
              {{ userDetail.name }}
              <van-tag round type="danger">lv{{ userDetail.level }}</van-tag>
            </p>
          </div>
          <div class="signature opacity8">
            <p>{{ userDetail.signature }}</p>
          </div>
          <div class="user-fans">
             <span>{{ userDetail.follows }}</span>
             <span>{{ userDetail.followeds }}</span>
          </div>

        </div>

      </div>
      <my-bar @isSticky="isSticky" class="my-detail">
        <template v-slot:home>
          <item-cent title="" describe="" @click.native="showSongRanking"/>
        </template>
        <template v-slot:my>

        </template>
      </my-bar>
    </van-popup>

  </div>
</template>

<script>
import {Toast} from 'vant';
import {getUserDetail} from "network/my";
import MyBar from "./childMy/myBar";
import TopBar from "../../components/common/TabBar/topBar";
import ItemCent from "../../components/content/itemCent/ItemCent";

export default {
  name: "my",
  components: {ItemCent, TopBar, MyBar},
  data() {
    return {
      show: true,
      showShare: false,
      topStyle: '',
      options: [
        {name: '', icon: 'wechat'},
        {name: '', icon: 'weibo'},
        {name: '', icon: 'link'},
        {name: '', icon: 'poster'},
        {name: '', icon: 'qrcode'},
      ],
      userDetail: {
        name: '',           //
        id:'',              //id
        level: '',           //
        backUrl: '',        //
        avatarUrl: '',      //
        signature: '',      //
        followeds: 0,       //
        follows: 0,           //
      }
    };
  },
  created() {
    getUserDetail(this.$store.state.myID).then(res => {
      console.log(res)

      this.userDetail.backUrl = res.profile.backgroundUrl
      this.userDetail.name = res.profile.nickname
      this.userDetail.id = res.profile.userId
      this.userDetail.level = res.level
      this.userDetail.avatarUrl = res.profile.avatarUrl
      this.userDetail.signature = res.profile.signature
      this.userDetail.followeds = res.profile.followeds
      this.userDetail.follows = res.profile.follows
      this.userDetail.follows = res.profile.follows
      console.log(this.userDetail.id)

    })
  },
  methods: {
    onClickLeft() {
      this.$store.state.isBar = true
      this.$store.state.isMusic = true
      this.$router.back()
      Toast('')
    },
    onClickRight() {
      this.showShare = true
      Toast('')
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false
    },
    isSticky(isFixed) {
      console.log(isFixed)
      if (isFixed.isFixed) {
        this.topStyle =
            "background:url(" + this.userDetail.backUrl + ");" +
            "background-size: 100%;" +
            "background-position: bottom;" +
            "filter: brightness(80%);"
      } else {
        this.topStyle = ''
      }
    },
    //
    showSongRanking(){
      console.log('111')
      this.$router.push({
        name:'listenSong',
        params:{
          id:this.userDetail.id
        }
      })
    }
  },
}
</script>

<style scoped>
.my{
  position: relative;
  top: 0;
  left: 0;
  z-index: 500;
}
.user-page {
  width: 100vw;
  height: 90vw;
}

.img-back {
  filter: brightness(70%)
}

.van-hairline--bottom::after {
  border: none;
}

.user-detail {
  position: absolute;
  top: 20vw;
  left: 0;
  padding: 10px 16px;
  color: #ffffff;
}

.van-image {
  vertical-align: bottom;
}

.user-img {
  display: inline-block;
  overflow: hidden;
  border-radius: 50%;
}

.user-name {
  font-size: 18px;
  font-weight: 700;
  padding: 20px 0 10px;
}

.opacity8{
  opacity: 80%;
}

.user-fans{
  padding: 5px 0;
  font-size: 18px;
  font-weight: 600;
}

.signature {
  padding: 5px 0;
  line-height: 25px;
}

.my-detail {
  border-radius: 10px 10px 0 0;
  overflow: hidden;
}
</style>